<template>
	<view>
		<view class="top_hr"></view>
		<view style="position: fixed;top: 0;width: 100%;">
			<headers title="发布视频" :leftIcon="true" />
		</view>
		
		
		<view class="box">
			<!-- <textarea  v-model="texa" placeholder="编写文字" /> -->
			<image @click="addVideo" v-if="!flog" src="../../../static/club/add.png" mode="widthFix"></image>
			<video style="margin-bottom: 30rpx;" v-if="flog" object-fit="cover" :poster="controimg" :src="videoUrl" controls></video>
			
			<view class="hr"></view>
			<view class="texa">
				<view class="title">
					<text>添加文字</text>
					<text style="color: #999999;">({{texa.length}}/300)</text>
				</view>
				<textarea class="textarea"  v-model="texa" maxlength="300" placeholder="点击此处输入文字" />
			</view>
			<view class="send" @click="rightClick"><text>发布</text></view>
		</view>
		
		
		<uni-popup type="bottom" ref="popup" >
		<!-- :maskClick="false" -->
			<view class="pox">
				<view @click="choose(1)">发布到当前渔场</view>
				<view @click="choose(0)" v-if="is_masteruser==='1'">发布到首页推荐</view>
			</view>
		</uni-popup>
		
		<uni-popup type="center" ref="loading" >
			<view class="loading">
				<text>{{loadingText}}</text>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import headers from '@/components/header.vue';
	import popup from '@/components/uni-popup/uni-popup.vue'
   // #ifdef APP-PLUS
   const videoCompress = uni.requireNativePlugin('Carlos-VideoCompress');
   // #endif
	export default {
		components:{headers,popup},
		data() {
			return {
				texa:'',
				flog:false,
				videoUrl:'',
				token:'',
				roomId:'',
				userId:'',
				videoData:'',
				index:1,
				is_masteruser:'',
				controimg:'',
				controimgData:'',
				loadingText:''
			};
		},
	     async onLoad(e) {
			let data = uni.getStorageSync('userinfo');
			this.token = data.token;
			// this.type = getApp().globalData.videoMsg.type;
			// this.list = getApp().globalData.videoMsg.list;
		},
		methods:{
			async rightClick(){
			let	obj = {
					token:this.token,
					circle_content:this.texa,
					circle_video:this.videoData,
					controimg:this.controimgData
				}
				let res = await this.$Post('/api/Groundcircle/AddCircleVideo',obj);
				this.$Toast(res.data.msg);
				console.log(res)
				if(res.data.code===1){
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},700)
				}
			},//打开弹窗
			async choose(ind){
				this.index = ind;
				let obj = {};
				if(this.index){
					obj = {
						token:this.token,
						fishground_id:this.roomId,
						circle_content:this.texa,
						circle_video:this.videoData
					}
				}else{
					obj = {
						token:this.token,
						circle_content:this.texa,
						circle_video:this.videoData,
						controimg:this.controimgData
					}
				}
				
				
				let res = await this.$Post('/api/Groundcircle/AddCircleVideo',obj);
				this.$Toast(res.data.msg);
				// console.log(res)
				if(res.data.code===1){
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},700)
				}
			},//选择发布位置
			async addVideo(){
				// console.log(videoCompress)
				uni.chooseVideo({
					// sourceType:['album','camera'],
					success: (res) => {
						console.log(res.tempFilePath)
						console.log(plus.io.convertLocalFileSystemURL(res.tempFilePath))
						console.log(res.tempFilePath.indexOf('_doc'))
						let mobileUrl = '';
						if(res.tempFilePath.indexOf('_doc')===0||res.tempFilePath.indexOf('_www')===0){
							mobileUrl = plus.io.convertLocalFileSystemURL(res.tempFilePath);
						}else{
							mobileUrl=res.tempFilePath;
						}
						
						uni.showLoading({
							title:'正在压缩'
						})
						this.yaSuo(mobileUrl,async(path)=>{
							console.log(path);
							if(path){
								console.log(path);
								// uni.hideLoading();
								let arr =  path.split('.');
								if(arr[1]==='3gp'){
									console.log('11111')
									this.$Toast('视频文件格式无法上传');
									uni.hideLoading();
									return
								}
								console.log(path);
								let data = await this.getRequestMsg(path);
								console.log(data)
								this.videoUrl = data.website+data.url;
								this.videoData = data.url;
								if(data.video_imgurl){
									uni.hideLoading();
								this.controimg = data.website+data.video_imgurl;
								this.controimgData = data.video_imgurl;	
								}
								uni.hideLoading();
								this.flog = true;
							}else{
								uni.hideLoading();
								this.$Toast('压缩失败');
							}
						})
						// uni.showLoading();
						// console.log(res);
						
					
						// console.log(res.tempFilePath);
					},
					fail: (err) => {
						console.log(err)
					},
					complete: (a) => {
						console.log(a)
					}
				})
			},
			yaSuo(source,callback){
				videoCompress.compress({source,bitRate:55000,size:2048}, (result) => {
					console.log(result)
					if(result.status==='success'){
						// console.log(result.path);
						callback(result.path);
					}
					
				});
			},
			getRequestMsg(msg){
				return new Promise((resolve,reject)=>{
					uni.uploadFile({
					            url: `${this.baseUrl}api/common/upload`, 
					            filePath: msg,
					            name: 'file',
								formData: {
								    'token':this.token
								},
					            success: (uploadFileRes) => {
									
									let msg = JSON.parse(uploadFileRes.data);
									console.log(uploadFileRes)
									if(msg.code===1){
										if(uploadFileRes.data.msg){
											this.$Toast(uploadFileRes.data.msg);
										}
										let msg = JSON.parse(uploadFileRes.data);
										resolve(msg.data);
									}else if(uploadFileRes.statusCode==413){
										this.$Toast('文件过大，请先压缩');
									}
					            },
								fail(err) {
									console.log(err)
									reject(err)
								}
					        });
				})
			},//获取拿到数据发送后的返回数据
		}
	}
</script>

<style lang="scss" scoped>
	
	.send{
		width: 60%;
		margin: 0 auto;
		font-size: 26rpx;
		color: white;
		background: #52D2F6;
		border-radius: 8rpx;
		padding: 25rpx 0;
		text-align: center;
	}
	.texa{
		width: 100%;
		box-sizing: border-box;
		padding: 20rpx;
		.title{
			font-size: 26rpx;
			color: #333333;
			margin-bottom: 15rpx;
		}
		.textarea{
			border: none;
			color: #999999;
			font-size: 26rpx;
		}
	}
	.hr{
		width: 100%;
		height: 10rpx;
		background: #F0F0F0;
		margin-bottom: 30rpx;
	}
.top_hr{
		width: 100%;
		height: 160rpx;
	}
	.box{
		width: 100%;
		box-sizing: border-box;
		padding: 40rpx 20rpx;
		textarea{
			width: 100%;
			border: 1px solid #333333;
		}
		image{
			width: 100rpx;
		}
	}
	
	.pox{
		width: 100%;
		background: white;
		view{
			width: 100%;
			text-align: center;
			border-bottom: 1px solid #808080;
			padding: 20rpx 0;
			font-size: 32rpx;
		}
	}
</style>
